
























import Vue from 'vue';
import {Component, Prop} from 'vue-property-decorator';
import dayjs from 'dayjs';

@Component
export default class NumberPad extends Vue {
  @Prop() amount!: number;
  output = this.amount.toString(); //这个句子只在一开始执行一次！！


  x(isoString: string) {
    return dayjs(isoString).format('YYYY-MM-DD');
  }


  inputContent(event: MouseEvent) {
    const button = (event.target as HTMLButtonElement); //强制指定为button
    const input = button.textContent!;
    if (this.output.length === 16) return;
    //已经存在'.'了
    if (this.output.indexOf('.') > -1 && input === '.') {
      return;
    }
    //第一个输入
    if (this.output === '0') {
      if ('0123456789'.indexOf(input) > -1) {
        this.output = input;
        return;
      }
    }
    this.output += input;
  }

  remove() {
    if (this.output.length === 1) {
      this.output = '0';
      return;
    }
    this.output = this.output.slice(0, -1);
  }

  clear() {
    this.output = '0';
  }

  ok() {
    this.$emit('update:amount', parseFloat(this.output));
    this.$emit('submit');
    this.output = '0';
  }
}
